.card {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
    width: 100%;
    padding-top: 20px;
    background-color:olive;
    border: 1px solid #000000;
    border-radius: 5px;
    margin-bottom: 20px;
  }
  
  .image {
    width: 50%;
    background-color: white;
    border: 1px solid transparent;
    border-radius: 50%;
    margin-bottom: 50px;
  }
  
  .imageContainer {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 100%;
    border-bottom: 1px solid black;
    & p {
      margin-bottom: 20px;
    }
  }
  
  .list {
    display: flex;
    width: 100%;
  }
  
  .listItems {
    display: flex;
    flex-direction: column;
    width: calc(100% / 3);
    height: 100px;
    justify-content: center;
    align-items: center;
    &:not(:last-child) {
      border-right: 1px solid black;
    }
    & span:first-child {
      margin-bottom: 10px;
    }
    & span:last-child {
      font-weight: bold;
    }
  }
  
  @media screen and (min-width: 768px) {
    .card {
      width: calc((100% / 2) - 10px);
      margin-right: 20px;
    }
  }
