.cardTable {
    table-layout: fixed;
    width: 100%;
    border-collapse: collapse;
    background-color: darkgrey;
    border: 1px solid #000000;
    margin-bottom: 20px;
  }
  
  .tableHead {
    background-color: olive;
  }
  
  .tableHeadItem,
  .tableBodyItem {
    width: calc(100% / 3);
    text-align: center;
    border: 1px solid black;
  }
  
  .tableHeadItem {
    padding-top: 15px;
    padding-bottom: 15px;
  }
  
  .tableBodyItem {
    padding-top: 5px;
    padding-bottom: 5px;
  }
  
  .tableBody {
    &:nth-child(odd) {
      background-color: cornsilk;
    }
  }
  
  @media screen and (min-width: 768px) {
    .cardTable {
      width: calc((100% / 2) - 10px);
      margin-bottom: 0px;
    }
  }