body {
    background-color: darkseagreen;
  }
  
  .container {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
    width: 320px;
    margin-right: auto;
    margin-left: auto;
    padding: 20px;
  }
  
  @media screen and (min-width: 768px) {
    .container {
      width: 768px;
      padding-right: 34px;
      padding-left: 34px;
    }
  }
  @media screen and (min-width: 1200px) {
    .container {
      width: 1200px;
      padding-right: 75px;
      padding-left: 75px;
    }
  }